@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
body{font-family:"Rubik",sans-serif !important}body .topbar{width:100%;background-color:#8b2131;height:43px}body .topbar .row{width:100%}body .topbar .col-top-nav.col-lg-4{height:43px;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}body .topbar .col-top-nav.col-lg-4 a.top-nav-text{color:#fec524;-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;transition-duration:.3s}body .topbar .col-top-nav.col-lg-4 :hover{-webkit-transform:translateY(-8px);transform:translateY(-8px);transition-duration:.3s;transition-timing-function:ease-out;text-decoration:none}body .header-middle{height:100px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center}body .header-middle .col-header-nav{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}body .header-middle a.nav-link{color:#0e2240}body .header-middle .nav-pills .nav-link.active,body .header-middle .nav-pills .show>.nav-link{background-color:#0e2240}body .header-middle .icon-block{height:40px;transition:color .3s ease;cursor:pointer;padding-left:10px;padding-right:10px;width:40px;background:#f2f2f2;border-radius:50%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;margin-left:10px;color:#000;border:none}body .header-middle .col-header-nav.iconos.col-lg-3{-webkit-justify-content:flex-end;justify-content:flex-end}body .menu-header{height:53px;background-color:#0e2240}body .menu-header .menu-principal.nav.nav-pills{margin:0 auto;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-content:center;align-content:center;height:53px}body .menu-header a.nav-link{color:#fff;text-transform:uppercase}body .menu-header .nav-pills .nav-link.active,body .menu-header .nav-pills .show>.nav-link{color:#fec524;background-color:transparent}body .carrousel .slide img{width:100%}body .carrousel .slick-next{right:22px}body .carrousel .slick-prev{left:10px;z-index:1}body .carrousel .slick-prev:before,body .carrousel .slick-next:before{font-size:35px;color:#fff}body .bloque-info.col-lg-4{height:100px;padding-left:30px;padding-right:30px}body .medio.col-lg-4{border-right:1px solid #0e2240;border-left:1px solid #0e2240}
